.App {
  text-align: center;
}

.Container {

  border-radius: 20px;
  padding: 30px;
  margin-left: 200px, auto;
  margin-right: 200px, auto;
  margin-top: 50px;
  background-color: rgb(255, 219, 237);
  -webkit-box-shadow: inset 0px 0px 66px -3px rgba(255, 0, 255, 1);
  -moz-box-shadow: inset 0px 0px 66px -3px rgba(255, 0, 255, 1);
  box-shadow: inset 0px 0px 66px -3px rgba(255, 0, 255, 1);
 transition-duration: 1s;
}



.image {

  width: 50px;
}

#startButton {

  font-size: 17px;
  padding: 12px 28px;
  border-radius: 8px;
  background-color: rgb(190, 27, 149);
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  border: none;
  transition-duration: 0.3s;
}

#startButton:hover {
  background-color: white;
  color: rgb(190, 27, 149);
}

button {

  font-size: 14px;
  padding: 8px 12px;
  margin: 10px;
  border-radius: 8px;
  background-color: rgb(255, 255, 255);
  color: rgb(190, 27, 149);
  text-align: center;
  text-decoration: none;
  display: inline-block;
  border: none;
  transition-duration: 0.3s;
}

button:hover {


  background-color: rgb(190, 27, 149);
  color: white;
  -webkit-box-shadow: 0px 0px 66px -3px rgba(255, 0, 255, 1);
  -moz-box-shadow: 0px 0px 66px -3px rgba(255, 0, 255, 1);
  box-shadow: 0px 0px 66px -3px rgba(255, 0, 255, 1);
}